<template>
  <el-container ref="homePage">
    <el-container>
      <el-aside width="200px" height="100vh" style="background:#545c64 ">
        <el-menu
          default-active="$route.path"
          @select="handleOpen"
          class="el-menu-vertical-demo"
          background-color="#545c64"
          text-color="#a7b1c2"
          active-text-color="#fff"
        >
          <!-- <el-menu-item index="3">
                        <span class="iconfont icon-huodongguanli" style="font-size:20px;"></span>
                        <span slot="title" >活动管理</span>
                    </el-menu-item>
                    <el-menu-item index="4">
                        <span class="iconfont icon-shuffling-banner" style="font-size:16px;"></span>
                        <span slot="title">banner</span>
                    </el-menu-item>
                    <el-menu-item index="5">
                        <span class="iconfont icon-shenqingguanli"
                              style="font-size:18px;"></span>
                        <span slot="title">申请管理</span>
                    </el-menu-item>
                    <el-menu-item index="6">
                        <span class="iconfont icon-cardb" style="font-size:18px;"></span>
                        <span slot="title">卡版管理</span>
                    </el-menu-item>
                    <el-menu-item index="7">
                        <span class="iconfont icon-yijianfankui" style="font-size:18px;"></span>
                        <span slot="title">意见反馈</span>
                    </el-menu-item>
                    <el-menu-item index="8">
                        <span class="iconfont icon-zhaopinguanli" style="font-size:18px;"></span>
                        <span slot="title">招聘申请</span>
                    </el-menu-item>
                    <el-menu-item index="9">
                        <span class="iconfont icon-icon-test" style="font-size:16px;"></span>
                        <span slot="title">订单管理</span>
                    </el-menu-item>
                    <el-menu-item index="10">
                        <span class="iconfont icon-tousu" style="font-size:21px;"></span>
                        <span slot="title" >投诉管理</span>
                    </el-menu-item>
                    <el-menu-item index="11">
                        <span class="iconfont icon-icon_fuzaguanxiwangluo" style="font-size:15px;"></span>
                        <span slot="title">关系网管理</span>
                    </el-menu-item>
                    <el-menu-item index="12">
                        <span class="iconfont icon-yinhangguanli_icox"
                              style="font-size:19px;"></span>
                        <span slot="title">银行管理</span>
                    </el-menu-item>
                    <el-menu-item index="13">
                        <span class="iconfont icon-huaban"
                              style="font-size:18px;"></span>
                        <span slot="title">消息推送</span>
                    </el-menu-item>
                    <el-menu-item index="14">
                        <span class="iconfont icon-xitong-caozuorizhi"
                              style="font-size:15px;"></span>
                        <span slot="title">操作日志</span>
                    </el-menu-item>
                    <el-menu-item index="15">
                        <span class="iconfont icon-xiugaimima"
                              style="font-size:16px;"></span>
                        <span slot="title">修改密码</span>
          </el-menu-item>-->
        </el-menu>
      </el-aside>
      <el-main>
        <el-header
          style="background:#f3f4fc;width: 90%;position: fixed;top: 0;left: 199px;z-index: 999; border-bottom:1px solid #e7eaec"
          width="100%"
          height="70px"
        >
          <el-row :gutter="20" style="margin-right: 30px">
            <el-col :span="21" style="line-height: 70px;">
              <!-- <div class="grid-content bg-purple" style="font-size:22px;color: #686868;">欢迎使用卡与生活后台管理系统</div> -->
              <div class="navbar clearfix">
                <!-- 面包屑+动态路由 -->
                <el-breadcrumb class="breadcrumb-container" separator-class="el-icon-arrow-right">
                  <el-breadcrumb-item
                    v-for="item in levelList"
                    :key="item.path"
                    :to="item.path"
                  >{{item.meta.title}}</el-breadcrumb-item>
                </el-breadcrumb>
              </div>
            </el-col>
            <el-col :span="3">
              <div class="grid-content bg-purple" style="line-height: 70px;font-size:16px;">
                <span class="iconfont icon-user" style="color:#409EFF;font-size:18px"></span>
                <span class="ml5" style="color:#409EFF">{{phone}}</span>
                <span class="ml5" style="color: #686868;cursor:pointer;" @click="out()">退出</span>
              </div>
            </el-col>
          </el-row>
        </el-header>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "homePage",
  data() {
    return {
      phone: "",
      levelList: [],
      msgFormSon: "this is msg",
      clientHeight: "",
    };
  },
  watch: {
    $route() {
      this.getBreadcrumb();
    },
    // 如果 `clientHeight` 发生改变，这个函数就会运行
    clientHeight: function () {
      this.changeFixed(this.clientHeight);
    },
  },
  mounted() {
    this.btitle = this.$route.params.title;
    this.phone = localStorage.getItem("phone");
    this.tian = localStorage.getItem("tian");
    this.getBreadcrumb();

    // 获取浏览器可视区域高度
    this.clientHeight = `${document.documentElement.clientHeight}`;
    //document.body.clientWidth;
    //console.log(self.clientHeight);
    window.onresize = function temp() {
      this.clientHeight = `${document.documentElement.clientHeight}`;
    };
  },
  methods: {
    handleOpen(key, title) {
      switch (
        key
        // case '3':
        //     this.$router.push({name: 'campaignManagement',params:{title:'活动管理'}});
        //     // this.show = true;
        //     break;
        // case '4':
        //     this.$router.push({name: 'bannerList',params:{title:'banner'}});
        //     break;
        // case '5':
        //     this.$router.push({name: 'application',params:{title:'申请管理'}});
        //     break;
        // case '6':
        //     this.$router.push({name: 'cardManagement',params:{title:'卡版管理'}});
        //     break;
        // case '7':
        //     this.$router.push({name: 'giveFeedback',params:{title:'意见反馈'}})
        //     break;
        // case '8':
        //     this.$router.push({name: 'recruitingRequest',params:{title:'招聘申请'}})
        //     break;
        // case '9':
        //     this.$router.push({name: 'orderManagement',params:{title:'订单管理'}})
        //     break;
        // case '10':
        //     this.$router.push({name: 'complaintManagement',params:{title:'投诉管理'}});
        //     // this.show = true;
        //     break;
        // case '11':
        //     this.$router.push({name: 'networkManagement',params:{title:'关系网管理'}});
        //     break;
        // case '12':
        //     this.$router.push({name: 'bankManagement',params:{title:'银行管理'}});
        //     break;
        // case '13':
        //     this.$router.push({name: 'messagePush',params:{title:'消息推送'}});
        //     break;
        // case '14':
        //     this.$router.push({name: 'actionLog',params:{title:'操作日志'}});
        //     break;
        // case '15':
        //     this.$router.push({name: 'changePassword',params:{title:'修改密码'}});
        //     break;
      ) {
      }
    },
    out() {
      const h = this.$createElement;
      this.$msgbox({
        title: "提示",
        message: h("p", null, [h("span", null, "是否继续退出? ")]),
        showCancelButton: true,
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        beforeClose: (action, instance, done) => {
          if (action === "confirm") {
            instance.confirmButtonLoading = true;
            instance.confirmButtonText = "执行中...";
            setTimeout(() => {
              done();
              this.$router.push({
                path: "/",
                redirect: { name: "logo" },
              });
              setTimeout(() => {
                instance.confirmButtonLoading = false;
              }, 300);
            }, 1000);
          } else {
            done();
          }
        },
      }).then((action) => {
        this.$message({
          type: "info",
          message: "退出成功",
        });
      });
    },
    // 面包屑+动态路由
    getBreadcrumb() {
      let matched = this.$route.matched.filter((item) => item.name);
      const first = matched[0];
      if (
        first &&
        first.name.trim().toLocaleLowerCase() !== "homePage".toLocaleLowerCase()
      ) {
        matched = [
          { path: "/campaignManagement", meta: { title: "活动管理" } },
        ].concat(matched);
      }
      this.levelList = matched;
    },
    getMsgFormSon(data) {
      this.msgFormSon = data;
    },
    changeFixed(clientHeight) {
      //动态修改样式
      // console.log(clientHeight);
      // console.log(this.$refs.homePage.$el.style.height);
      this.$refs.homePage.$el.style.height = clientHeight - 20 + "px";
    },
  },
  computed: {
    defaultActive() {
      return "/" + this.$route.path.split("/").reverse()[0];
    },
  },
};
</script>
<style scoped>
/*.el-container >>> .el-main{
       overflow: hidden; 
    }*/
.el-main {
  display: block;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  -ms-flex-preferred-size: auto;
  flex-basis: auto;
  box-sizing: border-box;
  padding: 45px 0 20px 0;
}
.el-header >>> .el-row {
  margin-left: -10px;
  margin-right: 20px;
}
.el-menu {
  height: 100vh;
}

.ml5 {
  margin-left: 5px;
}

.el-menu-item is-active {
  color: #409eff;
}
.el-col >>> .el-breadcrumb {
  line-height: 5;
}
</style>


